<template>
	<view>
		<view class="head">
			详细信息
			<image src="../../static/fanhui.png" @tap="navBack()"></image>
		</view>
		<view v-if="ishead" class="group-apply" @tap="todealapply">
				<view>申请列表</view>
				<view class="tip" v-if="applyNum>0">{{applyNum}}</view>
		</view>
		<view class="group-user">
			<view class="group-user-info"  v-for="(item,index) in groupuserlist" :key="index">
				<image :src="serverUrl+item.imgurl"></image>
				<view>{{item.name}}</view>
			</view>		
		</view>
		<view class="look">查看全部</view>
		<view class="group-name">
			<view>群聊名称</view>
			<view>
				{{gname}}
				<image class="right" src="../../static/right.png"></image>
			</view>
		</view>
		<view class="group-notice">
			<view>群公告</view>
			<view class="notice">
				<view class="notice-left">
					{{gnotice}}
				</view>
				<view class="notice-right">
					<image class="right" src="../../static/right.png"></image>
				</view>			
			</view>
		</view>
		<view v-if="!ishead" class="group-out" @tap="outGroup">退出该群</view>
		<view v-if="ishead" class="group-out" @tap="deleteGroup">解散该群</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				gid:"",
				gname:"",
				gimg:"",
				ishead:false,
				uid:"",
				token:"",
				gnotice:"U盾四吃奶OA平均承诺粗大皇帝噶胡和传播阿囧吃葱为奥将从为阿赎回的催化崔好菜哦很粗出护士崔黄翠花对首次U盾hiUSD换成传才会删除导出删除此次删除",
				applyNum:0,
				groupuserNum:0,
				applylist:[],
				groupuserlist:[]
			}
		},
		onLoad(e) {
			this.gid = e.id
			this.gname = e.name
			this.gimg = e.img
			this.getstorage()
			this.getgroupdetail()
			this.getgroupuser()
		},
		methods: {
			getstorage() {
				try {
					const value = uni.getStorageSync('user');
					if (value) {
						this.uid = value.id,
						this.token = value.token
					}
				} catch (e) {
					console.log("取值失败")
				}
			},
			navBack(){
				uni.navigateBack()
			},
			todealapply(){
				uni.navigateTo({
					url:"../groupapply/groupapply"
				})
			},
			//获取群详情
			getgroupdetail(){
				uni.request({
					url:this.serverUrl+"/group/detail",
					method:"POST",
					data:{
						id:this.gid
					},
					success: (res) => {
						this.gnotice=res.data.result.notice
						if(this.uid==res.data.result.userID){
							this.ishead=true
						}
					}
				})
			},
			//获取群成员
			getgroupuser(){
				uni.request({
					url:this.serverUrl+"/group/getgroupuser",
					method:"POST",
					data:{
						gid:this.gid
					},
					success: (res) => {
						let data = res.data.result
						console.log(data)
						for(let i=0;i<data.length;i++){
							if(data[i].state==0){
								this.applylist.push(data[i])
								this.applyNum=this.applyNum+1
							}else{
								this.groupuserlist.push(data[i])
								this.groupuserNum=this.groupuserNum+1
							}
						}
						localStorage.setItem("applylist",JSON.stringify(this.applylist))
						localStorage.setItem("groupuserlist",JSON.stringify(this.groupuserlist))
					}
				})
			},
			//解散该群
			deleteGroup(){
				uni.showModal({
				    title: '提示',
				    content: '确定解散该群?',
				    success: function (res) {
				        if (res.confirm) {
				            uni.request({
								url:this.serverUrl+"/group/deletegroupuser",
								method:'POST',
								data:{
									groupID:this.gid,
									token:this.token
								},
								success:(res=>{
									if(res.data.status==200){
										uni.switchTab({
											url:"../group/group"
										})
									}else{
										uni.showToast({
											title:"服务器出错",
											icon:'none'
										})
									}
								})
							})
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			},
			//退出该群
			outGroup(){
				uni.showModal({
				    title: '提示',
				    content: '确定退出该群?',
				    success:(res=>{
						if (res.confirm) {
							console.log(this.serverUrl+"/group/deletegroupuser")
						    uni.request({
								url:this.serverUrl+"/group/outGroup",
								method:'POST',
								data:{
									groupID:this.gid,
									userID:this.uid,
									token:this.token
								},
								success:(res=>{
									if(res.data.status==200){
										uni.switchTab({
											url:"../group/group"
										})
									}else{
										uni.showToast({
											title:"服务器出错",
											icon:'none'
										})
									}
								})
							})
						} else if (res.cancel) {
						    console.log('用户点击取消');
						}
					})			        
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.head{
		width: 100%;
		height: 88rpx;
		background: #ffffff;
		box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.1);
		text-align: center;
		position: relative;
		line-height: 88rpx;
		padding-top: var(--status-bar-height);
		image{
			width: 48rpx;
			height: 48rpx;
			position: absolute;
			left: 24rpx;
			bottom: 20rpx;			
		}
	}
	.group-apply{
		width: 100%;
		height: 80rpx;
		line-height: 80rpx;
		display: flex;
		padding: 20rpx 0 20rpx 20rpx;
		justify-content: flex-start;
		border-bottom: 20rpx solid #D5D5D6;
		.tip{
			margin-left: 20rpx;
			text-align: center;
			width: 70rpx;
			height: auto;
			border-radius: 50%;
			background: red;
		}
	}
	.group-user{
		padding-top: 20rpx;
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		height: 400rpx;
		overflow: hidden;
		.group-user-info{
			display: flex;
			align-items: center;
			flex-direction: column;
			margin-left: 3.5%;
			width: 16%;
			height: 160rpx;
			image{
				border-radius: 15%;
				width: 100%;
				height: 100%;
			}
		}
		.more{
			max-width: 20%;
			margin-left: 4%;
			height: 120rpx;
			text-align: center;
		}
	}
	.look{
		color: #D5D5D6;
		text-align: center;
		padding-bottom: 20rpx;
		border-bottom: 20rpx solid #D5D5D6;
	}
	.group-name{
		width: 90%;
		padding: 20rpx;
		display: flex;
		justify-content: space-between;
		.right{
			padding-left: 10rpx;
			width: 28rpx;
			height: 28rpx;
		}
	}
	.group-notice{
		padding: 20rpx;
		display: flex;
		flex-direction: column;
		border-top: 2rpx solid #D5D5D6;
		border-bottom: 20rpx solid #D5D5D6;
		.notice{
			display: flex;
			align-items: center;
			font-size: 30rpx;
			color: #CCCCCC;
			.notice-right{
				width: 10%;
				.right{
					padding-left: 10rpx;
					width: 28rpx;
					height: 28rpx;
				}
			}
			.notice-left{
				padding: 20rpx 10rpx 0 10rpx;
				flex: 1;
				overflow : hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}
		}	
	}
	.group-out{
		width: 100%;
		height: 100rpx;
		border-bottom: 20rpx solid #D5D5D6;
		color: red;
		text-align: center;
		line-height: 100rpx;
	}
</style>
